<template>
  <div class="certification box" v-show="pageShow">
    <div v-if="checking" class="checking">
      <img src="../../assets/img/certification/deal.png"/>
      <div class="text1 font-bold">粉丝认证审核中</div>
      <div class="text2">平台正在审核您提交的资料，请耐心等待</div>
    </div>
    <!--三要素认证-->
    <div v-else>
      <div class="list flex space_between">
        <div class="list-l font-bold">手机号</div>
        <div class="list-r">{{mobile}}</div>
      </div>
      <div class="list flex space_between">
        <div class="list-l font-bold">选择证件</div>
        <div @click.stop="$Toast('抱歉，目前只支持居民身份证')" v-if="fromType==3" class="list-r flex">
          <div>居民身份证</div>
          <img src="../../assets/img/face/gray-click.png"/>
        </div>
        <div v-else class="list-r flex" @click.stop='pickerShow'>
          <div>居民身份证</div>
          <img src="../../assets/img/addCar/click.png"/>
        </div>
      </div>
      <div class="upload">
        <div class='list noBorder tl'>上传照片</div>
        <div class="flex upload-img space_between tc">
          <div @click.stop='openUploadDialog(1,"",tempCardType,false,getUploadMesApp1)' :key="('img1_'+uploadImg1.localImg)" class="upload-img1 bgCss" :style="{'background-image':'url('+(uploadImg1.res ? uploadImg1.localImg : upload1)+')'}">
            <div class="no-upload" v-if="!uploadImg1.res">
              <img src="../../assets/img/addCar/add.png"/>
              <div>扫描人像面</div>
            </div>
            <div v-else class="reset-upload">重新上传</div>
          </div>
          <div @click.stop='openUploadDialog(2,"",tempCardType,false,getUploadMesApp2)' :key="('img2_'+uploadImg2.localImg)" class="upload-img2 bgCss" :style="{'background-image':'url('+(uploadImg2.res ? uploadImg2.localImg : upload2)+')'}">
            <div class="no-upload" v-if="!uploadImg2.res">
              <img src="../../assets/img/addCar/add.png"/>
              <div>扫描国徽面</div>
            </div>
            <div v-else class="reset-upload">重新上传</div>
          </div>
        </div>
      </div>
      <!--上传人像面照片的信息-->
      <div v-if="uploadRes1 || uploadRes2 || $route.query.test==1">
        <div class="upload-mes">
          <div v-if="uploadRes1" class="flex space_between">
            <div class="upload-mes-l">真实姓名</div>
            <div class="upload-mes-r">{{uploadImg1.name}}</div>
          </div>
          <div v-if="uploadRes1" class="flex space_between">
            <div class="upload-mes-l">证件号</div>
            <div class="upload-mes-r">{{uploadImg1.id_number}}</div>
          </div>
          <div v-if="uploadRes1" class="flex space_between">
            <div class="upload-mes-l">地址</div>
            <div class="upload-mes-r">{{uploadImg1.address}}</div>
          </div>
          <div v-if="uploadRes2" class="flex space_between">
            <div class="upload-mes-l">证件起始日期</div>
            <div class="upload-mes-r">{{uploadImg2.startTime}}</div>
          </div>
          <div v-if="uploadRes2" class="flex space_between">
            <div class="upload-mes-l">证件截止日期</div>
            <div class="upload-mes-r">{{uploadImg2.endTime}}</div>
          </div>
        </div>
        <div v-if="$route.query.test==1" @click.stop="gotoRouter('writeMes')">人工审核</div>
        <div v-if="$route.query.test==1" @click.stop="gotoRouter('longTimeFace')">长期未登录</div>
        <div v-if="$route.query.test==1" @click.stop="gotoRouter('faceSuccess')">成功页面</div>
        <div class="change-upload tl">识别信息有误，<span @click.stop='addSgm(2)'>手动修改</span></div>
      </div>
      <div class="submit tc phone-pad">
        <div class="submit-text">信息仅用于实名认证，我们将严格保护您的数据和隐私安全</div>
        <div @click.stop="submitMes" class="btn font-bold" :class="(uploadRes1 && uploadRes2 ? 'check':'')">
          <div>提交</div>
        </div>
      </div>
    </div>
    
    <!--切换护照或者往来通行证-->
    <div v-if="errorShow" class="credentials-bg hiddenbg tc">
      <div class="credentials-bg-main">
        <div class="credentials-bg-top">
          <div v-if="errorType==1" class="text1">是否确认切换为护照</div>
          <div v-if="errorType==2" class="text1">是否确认切换为<br/>{{tempCardType==2 ? '台湾居民':'港澳居民'}}往来通行证</div>
          <div v-if="errorType==1 || errorType==2" class="text2">您选择的证件必须进行人工审核</div>
          <div v-if="errorType==3 || errorType==4 || errorType==6" class="text1">验证失败</div>
           <div v-if="errorType==7 || errorType==8 || errorType==12" class="text1">认证失败</div>
          <div v-if="errorType==3 || errorType==4 || errorType==6 || errorType==8" class="text2">请确保您的姓名、身份证号与手机号实名信息一致</div>
          <div v-if="errorType==5" class="text3">您的实名认证申请正在审核中<br/>暂无法进行此操作</div>
          <div v-if="errorType==7" class="text2">您的证件已过期</div>
          <div v-if="errorType==9" class="text3">{{errorTextMes}}</div>
          <div v-if="errorType==10" class="text3">手动修改识别信息<br/>将会进入刷脸认证</div>
          <div v-if="errorType==11" class="text1">识别失败</div>
          <div v-if="errorType==11" class="text2 tl">
            1.请保证图片焦点清晰，信息可读,不可太小或模糊不清<br/>
            2.不可有反光影响文字显示<br/> 
            3.请让四个边角完整展示，不要用手遮挡证件<br/> 
            4.请不要自行加水印<br/> 
            5.请不要使用扫描件或复印件<br/>
          </div>
          <div class="text2" v-if="errorType==12 || errorType==14 || errorType==15">{{errorTextMes}}</div>
          <div class='text1' v-if="errorType==13">提交成功</div>
          <div class='text2' v-if="errorType==13">我们将尽快审核您的资料</div>
        </div>
        <div class="flex font-bold toast-mybtn">
          <div @click.stop='addSgm(0)' v-if="errorType==8 || errorType==10 || errorType==11" class="munual-btn1">重新识别证件</div>
          <div @click.stop='isHideDialog' v-if="errorType==9" class="munual-btn1">返回</div>
          <div @click.stop='clickHandler' v-if="errorType==1 || errorType==2 || errorType==3" class="munual-btn1">{{errorType==3 ? '再试一次':'取消'}}</div>
          <div v-if="errorType==1 || errorType==2 || errorType==3 || errorType==8 || errorType==9 || errorType==10" class="line"></div>
          <div @click.stop='addSgm(1)' v-if="fromType!=3 && (errorType==1 || errorType==2 || errorType==3 || errorType==4)" class="munual-btn2">启用人工审核</div> 
          <div @click.stop='addSgm(3)' v-if="(errorType==6 || errorType==8 || errorType==10 || errorType==11)"  class="munual-btn2">刷脸认证</div>
          <div @click.stop='errorShow=false;errorType=-1;' v-if="errorType==5 || errorType==7 || errorType==12" class="munual-btn2">我知道了</div>
          <div @click.stop="loginPage" v-if="errorType==9" class="munual-btn2">切换帐号</div>
          <div @click.stop='gotoSuccess' v-if="errorType==13 || errorType==14 || errorType==15" class="munual-btn2">我知道了</div>
        </div>
      </div>
    </div>
    <!--选择身份类型-->
    <Picker ref="Picker" @getPicker='getPicker'></Picker>
    <!--选择本地图片-->
    <uploadImage ref="uploadImage" @localImage='localImage' :uploadIndex="uploadIndex"></uploadImage>
    <div v-if="authCheckStatus==1" class="fixed">待审核中!!!!</div>
  </div>
</template>

<script>
  import upload1 from '../../assets/img/certification/upload1.png';
  import upload2 from '../../assets/img/certification/upload2.png';
  import pickerMixins from 'mixins/pickerMixins.js'
  import uploadImgMixins from 'mixins/uploadImgMixins.js'
  
  import {queryMmcUserType,getThreeElementsAuthToken,transferBuickFansFromUser,transferBuickFansFromAuthed} from 'fetch/api.js'
  import bridge from 'fetch/bridge.js'
  
  let Base64 = require('js-base64').Base64
  
  export default{
    name:'certification',
    mixins:[pickerMixins,uploadImgMixins],
    data(){
      return {
        checking:false,
        mobile:'',//手机号
        upload1,
        upload2,
        // errorShow:false,//是否显示切换护照或者往来通行证弹框
        /*1=>切换护照 2=>切换往来通行证 刷脸认证<3次=>3  刷脸认证>=3次=>4 
        您的实名认证申请正在审核中 暂无法进行此操作=>5
        三要素失败>=3 =>6 身份证过期=>7  8=>三要素证件不通过 9=>用户已存在 手动修改=>10
        OCR识别失败=>11 其他错误=>12
        */
        // errorType:11,
        uploadImg1:{url:'',res:'',localImg:'',name:'',id_number:'',address:''},
        uploadImg2:{url:'',res:'',localImg:'',validity:'',startTime:'',endTime:''},
        // tempCardType:0,
        // CardType:0,//证件类型
        userType:1,//用户状态 ·1=>注册用户 2=>实名认证
        authCheckStatus:0,//人工审核状态 0=>无信息  1=>待审核  2=>通过 3=>拒绝
        // uploadTypeArr:['id_card','id_card_back'],
        errorNum:0,//累计提交错误次数
        stopClick:false,
        type:'',//是否是app
        pageShow:false,
        mobilePhone:'',
      }
    },
    watch:{
      tempCardType:function(index){
        console.log('监听临时选择证件的类型==',index)
        if(index!=0){
          //this.tempCardName='居民身份证';
          if(index==1){
            this.errorType=index
          }else{
            this.errorType=2
          }
          this.errorShow=true
        }
      },
    },
    created(){
      
      //this.$router.push({name:'face'})
      // this.errorType=8;
      // this.errorShow=true;//测试
      
     
      // this.transferBuickFansFromUserCall({
      //   resultCode:'1151',
      //   message:'其他错误提示',
      // })
      sessionStorage.isminiType=this.$route.query.clientType ? this.$route.query.clientType:'';
      
      this.$LoadingShow('加载中')
      let {idpUserId,clientId,accessToken,mobile,callback,type,fromType}=this.$route.query;
      this.fromType=fromType;//fromType：1: 仅粉丝认证 2:粉丝认证及加加车 3：长期未登录重新实名
      this.type=type;
      if(type=='app'){//app
        bridge('getUserMobile','',{callback:this.userMeesageBack,winName:'setUserMobile'})
        //bridge('getUserMessage','',{callback:this.userMeesageBack,winName:'getUserMessage'})
        
        //this.gotoSuccess()//已经通过的直接去成功页面
        
      }else{//这是h5
        if(idpUserId && clientId && accessToken){
          localStorage.setItem('idpUserId',idpUserId)
          localStorage.setItem('clientId',clientId)
          localStorage.setItem('accessToken',accessToken)
          localStorage.setItem('mobile',mobile)
          localStorage.setItem('myhref',decodeURIComponent(callback))
          this.mobile=mobile
          this.queryMmcUserType();//查询用户MMC实名认证状态
        }else{
          if(this.$route.query.test!=1){
            this.errorShow=true;
            this.errorType=14;
            this.errorTextMes='获取数据失败请稍后再试'
          }
        }
        this.$LoadingHide()
      }
      if(this.$route.query.test==1){
        this.$LoadingHide()
        this.pageShow=true
      }
     
      //this.$router.push({name:'addCar',query:{typeName:encodeURIComponent('居民身份证'),type:this.type}})
      
    },
    activated(){
      if(this.mobile){
        this.$LoadingHide()
      }
    },
    computed:{
      uploadRes1(){
        return this.uploadImg1.res
      },
      uploadRes2(){
        return this.uploadImg2.res
      },
    },
    methods:{
      authAction(eventName){//埋点
        if(this.$route.query.test==1 || this.type=='app'){
          try{
            bridge('authAction',{eventName})
          }catch(err){
            console.log('埋点失败--',eventName)
          }
        }
      },
      isHideDialog(){
        this.errorShow=false;
        this.errorType=-1;
        this.errorTextMes="";
        this.authAction('my_realname_submit_back')
      },
      loginPage(){
        this.authAction('my_realname_submit_change')
        bridge('closeAndGotoLoginPage')
      },
      addSgm(num){
        let errorType=this.errorType
        if(num==0){//重新识别证件
          if(errorType==10){
            this.authAction("my_realname_handchange_recheck")
          }else if(errorType==8){
            this.authAction("my_realname_submit_check")
          }else if(errorType==11){
            this.authAction("my_realname_rechecklose")
          }else{
            this.authAction("my_realname_recheck")
          }
          
          this.errorShow=false;
        }else if(num==1){//启用人工审核  
          this.authAction("my_realname_artificial")
          this.gotoRouter("writeMes")
          this.errorShow=false;
        }else if(num==2){//手动修改
          this.errorType=10;
          this.errorShow=true;
          this.authAction("my_realname_handchange")
        }else if(num==3){
          if(errorType==10){//刷脸认证
            this.authAction("my_realname_handchange_face")
          }else if(errorType==11){
            this.authAction("my_realname_facelose")
          }else if(errorType==6){//登录失败大于三次
            this.authAction("my_realname_submit_goface")
          }else if(errorType==8){//登录失败小于三次
            this.authAction("my_realname_submit_face")
          }
          this.gotoRouter("faceIndex")
          
        }
        
      },
      userMeesageBack(res){
        console.log('获取到用户信息=',res)
        res=res+'';
        this.mobilePhone=res;
        localStorage.setItem('mobile',res)
        this.mobile=res.substring(0,3)+'****'+res.substring(8);
        if(this.fromType!=3){
          bridge(
          "receiveLoadNetworkArgument",
          {
            path:'userInfo/queryMmcUserType',gateway:"private",params:{},method:"POST"
          },
          {
            callback:this.queryMmcUserTypeCall,winName:'acceptNewEnergyResultSet'
          }
          );//查询用户MMC实名认证状态
        }else{
          this.$LoadingHide()
          this.pageShow=true;
        }
        
      },
      gotoSuccess(){
        this.errorShow=false;
        console.log('这是什么==000',this.fromType,'type==',this.type)
        if(this.errorType==-1 && this.type!='app'){
          return this.$router.replace({name:'faceSuccess',query:{type:this.type,clientType:this.$route.query.clientType}})
        }
        if(this.errorType==15){
          this.errorType=-1;
          this.errorTextMes='';
          return
        }
        if(this.errorType==14){
          if (typeof WeixinJSBridge==='object'){
            document.addEventListener('WeixinJSBridgeReady', function(){ WeixinJSBridge.call('closeWindow'); }, false);
            WeixinJSBridge.call('closeWindow');
          }else{
            window.opener=null;
            window.open('about:blank','_self','').close()
          }
        }else{
          let clientType=sessionStorage.isminiType;
          let type=(this.type=='app');
          if(this.fromType==1){
            if(type){
              bridge('JoinSuccess')
            }
            this.$router.replace({name:'faceSuccess',query:{type:this.type,clientType}})
          }else if(this.fromType==2){
            if(type){
              bridge('JoinSuccess')
            }
            this.$router.replace({name:'addCar',query:{type:this.type,clientType}})
          }else if(this.fromType==3){
            if(type){
              bridge('JoinSuccess')
            }
            this.$Toast('实名认证已通过请重新登录')
            setTimeout(()=>{
              bridge('finishPage')
            },3000)
          }
        }
        this.errorType==-1;
      },
      async queryMmcUserType(){
        let res=await queryMmcUserType();//查询用户MMC实名认证状态
        this.queryMmcUserTypeCall(res);
      },
      async queryMmcUserTypeCall(res){
        console.log('查询用户MMC实名认证状态res=',res)
        if(res && res.resultCode=='0000'){
          let {authCheckStatus,userType}=res.data
          
          if(userType==2){
            if(this.type=='app'){
              bridge(
              "receiveLoadNetworkArgument",
              {
                path:'userAuth/transferBuickFansFromAuthed',gateway:"private",params:{},method:"POST"
              },
              {
                callback:this.transferBuickFansFromAuthedCall,winName:'acceptNewEnergyResultSet'
              }
              );
            }else{
              let res=await transferBuickFansFromAuthed()
              this.transferBuickFansFromAuthedCall(res)
            }
          }else if(userType==1){
            if(authCheckStatus==1){//提示审核中
              this.checking=true;
            }else{
              this.checking=false;
            }
          }
          
          this.pageShow=true;
        }else{
          this.$Toast(res.message)
        }
        this.$LoadingHide()
      },
      transferBuickFansFromAuthedCall(res){
        if(res && res.resultCode=='0000'){
          this.gotoSuccess()//已经通过的直接去成功页面
        }else{
          //这里是报错提示
          this.$Toast(res.message)
        }
      },
      clickHandler(){
        let errorType=this.errorType
        if(errorType==1 || errorType==2){
          this.errorShow=false
        }
        this.errorType=-1;
        this.tempCardType=0;
      },
      gotoRouter(name){//跳转到人工审核
        //errorType tempCardType==2 ? '台湾居民':'港澳居民'
        let typeName='居民身份证';
        if(this.errorType==1){
          typeName='护照';
        }else if(this.errorType==2){
          typeName='台湾居民往来通行证'
          if(this.tempCardType!=2){
            typeName='港澳居民往来通行证'
          }
        }
        this.$store.commit('SET_CERTCARDMES',{})
        let img1={...this.uploadImg1}
        if(name=='writeMes'){
          img1.file=''
          img1.localImg=''
          img1.url=''
        }
        console.log('img1==',img1)
        if(this.fromType==3 && name=='faceIndex'){
          name='longTimeFace'
        }
        this.$store.commit('SET_CERTCARDMES',{img1,img2:this.uploadImg2})
        //this.$router.push({name:'writeMes',query:{typeName:encodeURIComponent(typeName),type:this.$route.query.type}})
        this.$router.push({name,query:{typeName:encodeURIComponent(typeName),type:this.$route.query.type,clientType:this.$route.query.clientType}})
        this.errorShow=false;
      },
      getUploadMes(res,uploadIndex){//获取到证件信息
        console.log('获取到证件信息0000=',res,uploadIndex)
        if(res.resultCode=='0000'){
          let {data,key}=res.data;
          //let mes=this.BinToStr(this.base64ToBin(data));
          let mes=JSON.parse(Base64.decode(data))
          console.log('mes===00',mes)
          if(uploadIndex==1){
            let {name,id_number,address}=mes
            this.uploadImg1.res=true;
            this.uploadImg1.name=name;
            this.uploadImg1.id_number=id_number;
            this.uploadImg1.url=key;
            this.uploadImg1.address=address;
            console.log('name && id_number=',name , id_number,name && id_number)
            if(!(name && id_number)){
              this.errorType=11;
              this.errorShow=true;
              // this.uploadImg1.res=false;
              // this.uploadImg1.url='';
            }
          }else if(uploadIndex==2){
            //uploadImg2:{url:'',res:'',localImg:'',validity:'',startTime:'',endTime:''}
            let {validity}=mes
            //validity='';//测试
            this.uploadImg2.res=true;
            this.uploadImg2.url=key
            if(validity){
              this.uploadImg2.validity=validity;
              let time=validity.split("-");
              this.uploadImg2.startTime=time[0].replace(/\./g,'-');
              this.uploadImg2.endTime=time[1].replace(/\./g,'-');
            }else{
              this.uploadImg2.validity='';
              this.uploadImg2.startTime='';
              this.uploadImg2.endTime='';
              this.uploadImg2.res=false;
              this.uploadImg2.url='';
              this.errorType=11;
              this.errorShow=true;
            }
          }
        }else{
          if(uploadIndex==1){
            this.uploadImg1.res=false;
            this.uploadImg1.name='';
            this.uploadImg1.id_number='';
            this.uploadImg1.url='';
            this.uploadImg1.address='';
          }else{
            this.uploadImg2.validity='';
            this.uploadImg2.startTime='';
            this.uploadImg2.endTime='';
            this.uploadImg2.res=false;
            this.uploadImg2.url='';
            
          }
          this.errorType=11;
          this.errorShow=true;
        }
      },
      async submitMes(){//提交
        console.log('提交==stopClick=',this.stopClick)
        if(this.stopClick) return 
        this.stopClick=true
        if(!(this.uploadRes1 && this.uploadRes2)) return ;
        console.log('提交==111')
        let name=Base64.encode(this.uploadImg1.name)
        let authNumber=Base64.encode(this.uploadImg1.id_number)
        let address=Base64.encode(this.uploadImg1.address);
        let authJson={
          name,//用户姓名
          authNumber,//身份证
          address,//身份证地址
        }
        console.log('测试===',this.uploadImg2.startTime.replace(/-/g,'.')+'-'+this.uploadImg2.endTime.replace(/-/g,'.'),'address=',address,this.uploadImg1.address)
        this.$LoadingShow('加载中')
        this.authAction('my_realname_submit')
        if(this.type=='app'){
          let gateway='private'
          if(this.fromType==3){
            gateway='public'
            authJson.mobile=this.mobilePhone;
            authJson.name=this.uploadImg1.name;
            authJson.address=this.uploadImg1.address;
            authJson.authNumber=this.uploadImg1.id_number;
            console.log('参数===',authJson)
          }
          bridge(
          "receiveLoadNetworkArgument",
          {
            path:'common/getThreeElementsAuthToken',gateway:gateway,params:authJson,method:"POST"
          },
          {
            callback:this.getThreeElementsAuthTokenCall,winName:'acceptNewEnergyResultSet'
          }
          );
        }else{
          let res=await getThreeElementsAuthToken(authJson);//获取三要素认证凭证
          this.getThreeElementsAuthTokenCall(res)
        }
      },
      async getThreeElementsAuthTokenCall(res){
        this.$LoadingHide()
        let name=Base64.encode(this.uploadImg1.name)
        let authNumber=Base64.encode(this.uploadImg1.id_number)
        let address=Base64.encode(this.uploadImg1.address);
        // let authJson={
        //   name,//用户姓名
        //   authNumber,//身份证
        // }
        console.log('获取三要素认证凭证res=',res,Base64.decode(name),Base64.decode(authNumber))
        let code=res.resultCode
        this.stopClick=false
        if(res && code=="0000"){
          let authPassToken=res.data.authPassToken
          let userJson={
            name,
            address,
            authType:1,
            authNumber,
            authPassType:1,
            frontImgUrl:this.uploadImg1.url,
            backImgUrl:this.uploadImg2.url,
            authPassToken,
          }
          let time=this.uploadImg2.startTime.replace(/-/g,'.')+'-'+this.uploadImg2.endTime.replace(/-/g,'.')
          userJson.expireDate=time;
          if(this.type=='app'){
            let url='userAuth/transferBuickFansFromUser'
            let gateway='private'
            if(this.fromType==3){
              url='userAuth/longTimeAuthCreate'
              userJson.name=this.uploadImg1.name
              userJson.authNumber=this.uploadImg1.id_number;
              userJson.address=this.uploadImg1.address;
              userJson.mobilePhone=this.mobilePhone;
              
              gateway='public'
            }
            bridge(
            "receiveLoadNetworkArgument",
            {
              path:url,gateway:gateway,params:userJson,method:"POST"
            },
            {
              callback:this.transferBuickFansFromUserCall,winName:'acceptNewEnergyResultSet'
            }
            );
          }else{
            let result=await transferBuickFansFromUser(userJson);//普通用户升级为粉丝
            this.transferBuickFansFromUserCall(result)
          }
        }else if(code=='1151'){//证件号已被认证
          if(this.$route.query.type=='app'){
            this.errorType=9;
          }else{
            this.errorType=15;
          }
          this.errorShow=true;
          this.errorTextMes=res.message;//其他错误提示
        }else{
          this.errorShow=true;
          if(code=='1150'){
            this.errorNum=this.errorNum+1;//提交错误加一
            if(this.errorNum<3){//点提交返回1150错，即三要素识别不通过，弹框（三要素认证不通过 <3）
              this.errorType=8;
            }else{//点提交返回1150错，满3次，弹框（三要素认证-失败>=3次）
              this.errorType=6;
            }
          }else{
            this.errorType=12;
            this.errorTextMes=res.message;//其他错误提示
          }
        }
      },
      transferBuickFansFromUserCall(result){
        console.log('普通用户升级为粉丝result=',result)
        let code=result.resultCode;
        if(code=="0000"){
          //this.gotoSuccess()//已经通过的直接去成功页面
          // this.errorShow=true;
          // this.errorType=13;
          this.errorType=-1;
          this.gotoSuccess()
        }else if(code=='1151'){//证件号已被认证
          if(this.$route.query.type=='app'){
            this.errorType=9;
          }else{
            this.errorType=15;
          }
          this.errorShow=true;
          this.errorTextMes=result.message;//其他错误提示
        } else{
          //报错
          this.errorShow=true;
          this.errorType=12;
          this.errorTextMes=result.message;//其他错误提示
          console.log('报错===',this.errorType,this.errorTextMes)
        }
        this.stopClick=false
      },
    }
  }
</script>

